.post-container {
  background-color: rgb(238, 238, 238);
  height: 100vh;
  display: flex;
  .link {
    text-decoration: none;
    color: rgb(0, 0, 0);
  }

  .post {
    align-items: center;
    text-align: center;
    margin: auto;
    margin-top: 150px;
    margin-bottom: 80px;
    width: 400px;
    height: 380px;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    .title {
      font-weight: bold;
      font-size: 24px;
      margin-top: 20px;
      color: rgb(77, 77, 77);
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .details {
      display: flex;
      flex-direction: column;
      width: 300px;
      margin: auto;
      margin-top: 20px;

      input {
        margin-top: 6px;
        height: 30px;
        background-color: #fff;
        border: 0.6px solid rgb(167, 167, 167);
        padding: 5px;
        border-radius: 3px;
      }

      button {
        cursor: pointer;
        border-radius: 3px;
        height: 32px;
        margin-top: 20px;
        border: 0.6px solid rgb(167, 167, 167);
        background: rgb(236, 216, 145);
        background: rgb(236, 216, 145);
        background: rgb(236, 216, 145);
        background: linear-gradient(
          180deg,
          rgba(236, 216, 145, 1) 0%,
          rgba(195, 147, 64, 1) 100%
        );
        font-size: 16px;
      }
      button:hover {
        background: rgb(211, 186, 97);
        background: rgb(211, 186, 97);
        background: rgb(211, 186, 97);

        background: linear-gradient(
          180deg,
          rgb(233, 209, 122) 0%,
          rgb(194, 141, 50) 100%
        );
      }
      span {
        text-align: start;
        margin-top: 10px;
      }
    }
    .desc {
      margin: auto;
      width: 300px;
      display: flex;
      flex-direction: column;
      margin-top: 10px;

      button {
        cursor: pointer;
        border-radius: 3px;
        height: 32px;
        margin-top: 10px;
        border: 0.6px solid rgb(167, 167, 167);

        background: rgb(243, 243, 243);
        background: linear-gradient(
          180deg,
          rgba(243, 243, 243, 1) 0%,
          rgba(187, 187, 187, 1) 100%
        );
        font-size: 16px;
      }
      button:hover {
        background: rgb(243, 243, 243);
        background: linear-gradient(
          180deg,
          rgb(219, 219, 219) 0%,
          rgb(173, 173, 173) 100%
        );
      }
    }
  }
}
